import gql from 'graphql-tag'


export const REGISTER_USER = gql `
            mutation($username: String!, $email: String!, $avatar: Upload, $password: String!) {
              addUser(username: $username, email: $email, avatar:$avatar, password: $password) {
                email
              }
            }
          `;

export const ADD_BOOK = gql `
   mutation($name: String!, $genre: String!, $description: String $photo: Upload, $user_add: String,  $author_id: String!) {
     addBook( name: $name, genre: $genre, description: $description, photo: $photo, user_add: $user_add, author_id: $author_id ) {
        name
        genre
     }
   }
`

export const LIKED_BOOK = gql `
   mutation($bookId : String!) {
    likedBook( bookId: $bookId) {
      success
      error
    }
  }
`

export const UNLIKED_BOOK = gql `
  mutation($bookId : String!) {
    unlikedBook( bookId: $bookId) {
      success
      error
    }
  }
`