<template>
  <div class="header">
    <nav class="navigation">
      <h1 class="title-lg">Zuans</h1>
      <BurgerItem />
    </nav>
    <SideBarItem>
      <h2 class="sidebar-title">Zuans Book</h2>
      <div v-if="user" class="user-profile">
        <img :src="user.avatar" :class="{ 'img-avatar' : user.avatar}" id="avatar" alt />
        <h3>Hello {{ user.username }}</h3>
      </div>
      <ul class="sidebar-list">
        <li>
          <a href="/" @click="togglePanel(false)">Home</a>
        </li>
        <li>
          <a href="#" @click="toggleFeature">
            Book
            <font-awesome class="ml-1" :icon="['fas','caret-down']"></font-awesome>
          </a>
          <transition name="showFeature">
            <div v-if="showFeature" class="pt-3">
              <li>
                <a href="/feature/book-add" @click="togglePanel(false)">Add</a>
              </li>
              <li>
                <a href="/feature/book-search" @click="togglePanel(false)">Search</a>
              </li>
              <li>
                <a href="/feature/book-all" @click="togglePanel(false)">List Book</a>
              </li>
            </div>
          </transition>
        </li>
        <li>
          <a href="/#about" @click="togglePanel(false)">About</a>
        </li>
        <li v-if="user">
          <a href="/feature/book-like" @click="togglePanel(false)">Wishlist</a>
        </li>
        <li>
          <button v-if="!user" @click="goTo('login')" class="btn btn-success px-4 py-2">Login</button>
          <button v-else @click="goTo('logout')" class="btn btn-outline-light px-4 py-2">Logout</button>
        </li>
      </ul>
    </SideBarItem>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import BurgerItem from "./side-menu/MenuBurger";
import SideBarItem from "./side-menu/MenuSide";
export default {
  name: "myHeader",
  data() {
    return {
      dataUser: "",
      showFeature: true,
    };
  },
  components: {
    BurgerItem,
    SideBarItem,
  },
  methods: {
    ...mapActions(["userLogout"]),
    ...mapMutations(["togglePanel"]),
    goTo(url) {
      if (url == "logout") {
        this.userLogout();
        this.dataUser = false;
        return this.$router.go();
      }
      this.togglePanel(false);
      this.$router.push({ name: `${url}` });
    },
    toggleFeature() {
      this.showFeature = !this.showFeature;
    },
  },
  computed: {
    ...mapState(["auth"]),
    ...mapState({ getUser: "user" }),
    user() {
      if (localStorage.getItem("logIn")) {
        const {
          avatar: { filename },
          username,
        } = this.getUser;
        return {
          avatar: `http://localhost:5000/img-avatar/${filename}`,
          username,
        };
      }
      return null;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_variables.scss";

.header {
  background-color: transparent;
  width: 100%;

  .navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: inherit;
    z-index: 1;
    padding: 0.5rem 1rem;

    h1 {
      margin-top: 0;
      z-index: 2;
      color: white;
      font-size: 3rem;
      font-family: $poppins;
    }
  }

  .sidebar-list {
    padding: 2rem 0;
    li {
      color: #ffffff;
      font-family: $poppins;
      font-size: 1.4rem;
      list-style-type: none;
      text-align: left;
      margin-bottom: 1.3rem;
      margin-left: 2rem;

      h2 {
        font-size: 2.5rem;
      }

      a {
        display: block;
        color: inherit;
        text-decoration: none;
        font-family: inherit;
        transition: all 0.4s ease-out;
        transform: scale(1);
      }

      a:hover {
        transform: scale(1.2);
      }
    }
  }

  .sidebar-title {
    text-align: left;
    font-family: $poppins;
    font-size: 2.4rem;
    color: white;
  }

  .user-profile {
    display: flex;
    flex-direction: column;

    h3 {
      margin-top: 1rem;
      color: #eeeeee;
    }

    .img-avatar {
      height: 100px;
      margin: auto;
      width: 100px;
      border-radius: 50%;
      border: 4px solid #fcef42;
      margin-top: 1.4rem;
    }
  }
}
</style>