import {
    apolloClient
} from '../vue-apollo';
import {
    AUTH_USER
} from '../graphql/query';
import {
    REGISTER_USER
} from '../graphql/mutations'

import {
    QUERY_USER
} from '../graphql/query';

export default {
    userQuery: async ({
        commit,
        state
    }) => {
        try {
            const {
                data: {
                    user
                }
            } = await apolloClient.query({
                query: QUERY_USER,
            })
            await commit('SET_USER', user);
            return state.user;
        } catch (err) {
            throw new Error(err);
        }
    },

    userAuth: async ({
        commit,
        dispatch,
    }, authDetails) => {
        try {
            const {
                data: {
                    authUser
                }
            } = await apolloClient.query({
                query: AUTH_USER,
                variables: authDetails,
            });
            await commit('SET_AUTH', authUser);
            await dispatch('userQuery');
        } catch (error) {
            throw new Error(error);
        }
    },
    userRegister: async ({
        dispatch
    }, inputUser) => {
        const {
            username,
            email,
            password,
            avatar
        } = inputUser;
        const user = {
            email,
            password,
        }
        try {
            await apolloClient.mutate({
                mutation: REGISTER_USER,
                variables: {
                    username,
                    email,
                    password,
                    avatar
                }
            })
            await dispatch('userAuth', user);
        } catch (error) {
            throw new Error(error);
        }
    },
    userLogout: ({
        commit
    }) => {
        commit('LOGOUT_USER');
    }

}